.before\:absolute::before {
	content: "";
	position: absolute;
}
.before\:top-0\.5::before {
	content: "";
	top: 0.125rem;
}
.before\:right-0\.5::before {
	content: "";
	right: 0.125rem;
}
.before\:top-0::before {
	content: "";
	top: 0px;
}
.before\:right-0::before {
	content: "";
	right: 0px;
}
.before\:block::before {
	content: "";
	display: block;
}
.before\:h-6::before {
	content: "";
	height: 1.5rem;
}
.before\:w-6::before {
	content: "";
	width: 1.5rem;
}
.before\:-translate-x-7::before {
	content: "";
	--tw-translate-x: -1.75rem;
	-webkit-transform: var(--tw-transform);
	        transform: var(--tw-transform);
}
.before\:transform::before {
	content: "";
	-webkit-transform: var(--tw-transform);
	        transform: var(--tw-transform);
}
.before\:rounded-full::before {
	content: "";
	border-radius: 9999px;
}
.before\:bg-darkElements::before {
	content: "";
	--tw-bg-opacity: 1;
	background-color: hsla(209, 23%, 22%, var(--tw-bg-opacity));
}
.before\:bg-white::before {
	content: "";
	--tw-bg-opacity: 1;
	background-color: rgba(255, 255, 255, var(--tw-bg-opacity));
}
.before\:transition-all::before {
	content: "";
	transition-property: all;
	transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
	transition-duration: 150ms;
}
.hover\:bg-gray-200:hover {
	--tw-bg-opacity: 1;
	background-color: rgba(229, 231, 235, var(--tw-bg-opacity));
}
.group:hover .group-hover\:scale-110 {
	--tw-scale-x: 1.1;
	--tw-scale-y: 1.1;
	-webkit-transform: var(--tw-transform);
	        transform: var(--tw-transform);
}
.dark .dark\:bg-darkElements {
	--tw-bg-opacity: 1;
	background-color: hsla(209, 23%, 22%, var(--tw-bg-opacity));
}
.dark .dark\:fill-\[\#fff\] {
	fill: #fff;
}
.dark .dark\:text-darkText {
	--tw-text-opacity: 1;
	color: hsla(0, 0%, 100%, var(--tw-text-opacity));
}
.dark .dark\:placeholder-gray-300::-webkit-input-placeholder {
	--tw-placeholder-opacity: 1;
	color: rgba(209, 213, 219, var(--tw-placeholder-opacity));
}
.dark .dark\:placeholder-gray-300:-ms-input-placeholder {
	--tw-placeholder-opacity: 1;
	color: rgba(209, 213, 219, var(--tw-placeholder-opacity));
}
.dark .dark\:placeholder-gray-300::placeholder {
	--tw-placeholder-opacity: 1;
	color: rgba(209, 213, 219, var(--tw-placeholder-opacity));
}
.dark .dark\:hover\:brightness-125:hover {
	--tw-brightness: brightness(1.25);
	-webkit-filter: var(--tw-filter);
	        filter: var(--tw-filter);
}
