@tailwind base;
@tailwind components;
@tailwind utilities;
/* @import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap'); @import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap'); */

@layer components {
    *, *::after, *::before {
        @apply transition-colors;
    }

    body {
        @apply bg-lightBg dark:bg-darkBg;
        font-family: 'Nunito Sans', sans-serif;
    }

    .filter-button {}
    .filter-button button{
        @apply px-6 py-2 w-full text-left hover:bg-gray-200 dark:hover:bg-gray-600 transition-all;

    }

    #theme-switch::before {
        /*opacity: .2;*/

    }
    #theme-switch:hover::before {
        opacity: 0.8;
    }
    #theme-switch {
        /*background: url("/images/icon-moon.svg") no-repeat;*/
    }

    .card-p {
        margin-bottom: 2px;

    }
    .card-p:last-child {
        margin-bottom: -5px;
    }
      
    .info-p {
        flex: 1;
        flex-basis: 100%;
        margin-bottom: 13px;

    }   
    .info-p:nth-child(7) {
        flex-basis:  100%;
        
    }
    .info-p:last-child {
        flex-basis:  100%;
    }

    @media (min-width: 530px) {
        .info-p {
            flex: 1;
            flex-basis: 50%;
            margin-bottom: 13px;

        }   
        .info-p:nth-child(7) {
            flex-basis:  100%;
            
        }
        .info-p:last-child {
            flex-basis:  100%;
        }
    }
    @media (min-width: 830px) {
        .skeleton-p {
            flex: 1;
            flex-basis: 45%;
            width:  45%;
            margin-bottom: 23px;
        }
    }
    .skeleton-p {
        flex: 1;
        flex-basis: 100%;
        width:  100%;
        margin-bottom: 23px;
    }
    .skeleton-p:nth-child(7) {
        flex-basis:  100%;
        width:  100%;
    }
    .skeleton-p:last-child {
        flex-basis:  100%;
        width:  100%;
    }
    .skeleton {
        /*@apply animate-pulse;*/
        animation:  lightup 1000ms ease-in-out infinite alternate;
        background: linear-gradient(to top right, transparent, #84848442, transparent, #84848442, transparent) no-repeat;
        background-size: 1000px 1500px;
    }
    @keyframes lightup {
        0% {
            /*opacity: 0;*/
            /*filter: brightness(0.8);*/
            /*background: linear-gradient(0deg, transparent, #8484844a, transparent) no-repeat;*/
            background-position: 0 -1000px;
        }
        100% {
            /*opacity: 0;*/
            /*filter: brightness(0.8);*/
            /*background: linear-gradient(90deg, transparent, #8484844a, transparent) no-repeat;*/
            background-position: 0 0;
    
        }
    }
} 